import React, { useState } from "react";
import Insidenav from "../insidenav";
import { Link } from "react-router-dom";
import { Helmet } from 'react-helmet'
const TITLE = 'Number of Words Counter | Student Calculator';
function Add() {
  const [name1, setName1] = useState("");
  

  const [headingText, setHeading] = useState("");

  function handleChange1(event) {
    console.log(event.target.value);
    setName1(event.target.value);
  }
  function handlereset() {
    setName1("");
    setHeading("");
  }
  function countWords(str) {
         str = str.replace(/(^\s*)|(\s*$)/gi,"");
         str = str.replace(/[ ]{2,}/gi," ");
         str = str.replace(/\n /,"\n");
         return str.split(' ').length;
      }
     
  function handleClick(event) {
   
    var result = countWords(name1);
    setHeading(result);

    event.preventDefault();
  }

  return (
    <div>
        <Insidenav />
        <Helmet>
          <title>{ TITLE }</title>
        </Helmet>
    <div className="container-fluid calci">
      <h1 className="calci">Total Number of Words = {headingText}</h1>
            <p4>Paste Your Text Below</p4>

      <form onSubmit={handleClick}>
        <textarea
          cols="90"
          className="calciip-charcount"
          onChange={handleChange1}
          type="text"
          placeholder="NOTE : Pull the bottom right corner of this box to expand the area of this box for better reading ! "
          value={name1}
          required
        />
        
        <button className="calcibutton" type="submit">Submit</button>
        <button onClick={handlereset} className="calcibutton" type="reset">Reset</button>

      </form>
      <div className="">   
        <p className="suggestion">SUGGESTED CALCULATORS FOR YOU</p>
        <Link to="/writing/character-count"><button className="btn btn-light mr-3">Character Counter</button></Link>
        <Link to="/writing/line-count"><button className="btn btn-light mr-3">Lines counter</button></Link>
        <Link to="/basic-converters/time-converters"><button className="btn btn-light mr-3">Time and Date Converter</button></Link>

        <Link to="/basic-converters/memory-size-converters"><button className="btn btn-light mr-3">Memory Size Converter</button></Link>



      </div>
      </div>
      <div className="container mt-3 mb-3">
      <h4>About Total Number Of Words</h4>
      <p3>This calculator is made for anyone who wants to know the number of words they have written for their article, essay or anything of that sort. You don't have to struggle to find out whether you have crossed the word limit for a essay ! We are here for you !</p3>
      <br /><br /><h4>Why This ?</h4>
      <p3>WORD LIMIT EXCEEDED  <br /> Tired of hearing this ? Check Here Before you go and type there ! </p3>
      

    </div>
    </div>
  );
}

export default Add;
