import React, { useState } from "react";
import Insidenav from "../insidenav";
import { Link } from "react-router-dom";
import { Helmet } from 'react-helmet'
const TITLE = 'Energy Converters | Student Calculator';
var convert = require('convert-units')


function Add() {
  
  const [name1, setName1] = useState("");
  const [name2, setName2] = useState("");
  const [name3, setName3] = useState("");
  const [name4, setName4] = useState("");
  const [name5, setName5] = useState("");
  const [name6, setName6] = useState("");
  const [name7, setName7] = useState("");
 

  const { Convert } = require ('any-to-any');

  function handleChange1(event) {
    console.log(event.target.value);
    setName1(event.target.value);
    
  }function handleChange2(event) {
    console.log(event.target.value);
    setName2(event.target.value);
    
  }
  function handleChange3(event) {
    console.log(event.target.value);
    setName3(event.target.value);
    
  }
  function handleChange4(event) {
    console.log(event.target.value);
    setName4(event.target.value);
  }
  function handleChange5(event) {
    console.log(event.target.value);
    setName5(event.target.value);
  
  }
  function handleChange6(event) {
    console.log(event.target.value);
    setName6(event.target.value);
  
  }
  function handleChange7(event) {
    console.log(event.target.value);
    setName7(event.target.value);
  
  }
  function handlereset() {
    setName1("");
    setName2("");
    setName3("");
    setName4("");
    setName5("");
    setName6("");
    setName7("");
  
  }
  
 
  function handleClick(event) {
    if(name1!="" ||name2!="" ||name3!="" ||name4!="" ||name5!="" ||name6!="" ||name7!=""){
      var entered = 0;
    if(name1 != ""){entered++;}
    if(name2 != ""){entered++;}
    if(name3 != ""){entered++;}
    if(name4 != ""){entered++;}
    if(name5 != ""){entered++;}
    if(name6 != ""){entered++;}
    if(name7 != ""){entered++;}
 
    if(entered != 1){
      handlereset();
    }
    else{
    if(name1 != ""){var Input = name1; var From = 'Wh';}
    if(name2 != ""){var Input = name2; var From = 'J';}
    if(name3 != ""){var Input = name3; var From = 'kJ';}
    if(name4 != ""){var Input = name4; var From = 'mWh';}
    if(name5 != ""){var Input = name5; var From = 'kWh';}
    if(name6 != ""){var Input = name6; var From = 'MWh';}
    if(name7 != ""){var Input = name7; var From = 'GWh';}
   

    setName1(convert(Input).from(From).to('Wh'));
    setName2(convert(Input).from(From).to('J'));
    setName3(convert(Input).from(From).to('kJ'));
    setName4(convert(Input).from(From).to('mWh'));
    setName5(convert(Input).from(From).to('kWh'));
    setName6(convert(Input).from(From).to('MWh'));
    setName7(convert(Input).from(From).to('GWh'));
    }}
     event.preventDefault();
    
  }

  return (
    <div>
      <Insidenav />
      <Helmet>
          <title>{ TITLE }</title>
        </Helmet>
    <div className="container-fluid calci">

      <h1 className="calci">Enter in Any one of the following and click find</h1>

      <form onSubmit={handleClick} autoComplete="off">
      <label for="bin">Watt hour ( Wh ) ⇃↾</label>
      <input className="calciipsmallmany"  type="number" step="any" id="bin" onChange={handleChange1} value={name1} />
      <label for="oct">Joule ( J ) ⇃↾</label>
      <input className="calciipsmallmany"  type="number" step="any" id="oct" onChange={handleChange2} value={name2} />
      <label for="dec">Kilo Joule ( kJ ) ⇃↾</label>
      <input className="calciipsmallmany" type="number" step="any" id="dec" onChange={handleChange3} value={name3} />
      <label for="dec">milli Watt hour ( mWh ) ⇃↾</label>
      <input className="calciipsmallmany" type="number" step="any" id="dec" onChange={handleChange4} value={name4} />
      <label for="dec">kilo Watt hour ( kWh ) ⇃↾</label>
      <input className="calciipsmallmany"  type="number" step="any" id="dec" onChange={handleChange5} value={name5} />
      <label for="dec">Mega Watt hour ( MWh ) ⇃↾</label>
      <input className="calciipsmallmany"  type="number" step="any" id="dec" onChange={handleChange6} value={name6} />
      <label for="dec">Giga Watt hour ( GWh ) ⇃↾</label>
      <input className="calciipsmallmany"  type="number" step="any" id="dec" onChange={handleChange7} value={name7} />
     
     
      
        
        <button className="calcibutton" type="submit">Find Rest All</button>
        <button onClick={handlereset} className="calcibutton" type="reset">Reset</button>

      </form>
      <div className="">   
        <p className="suggestion">SUGGESTED CALCULATORS FOR YOU</p>
        <Link to="/advanced-converters"><button className="btn btn-light mr-3">More Converters Like this</button></Link>
        <Link to="/basic-converters"><button className="btn btn-light mr-3">Basic Converters</button></Link>
       
      </div>
    </div>
    <div className="container mt-3 mb-3">
    <h4>How to Use this ?</h4>
      <p3>Enter in any one field and then click find. If you type in more than one box, then you will not find any answer. Most importantly, make sure you have typed in only one box. Otherwise click reset and then continue.</p3>
     
    </div>
    </div>
  );
}

export default Add;
