import React, { useState } from "react";
import Insidenav from "../insidenav";
import { Link } from "react-router-dom";
import { Helmet } from 'react-helmet'
const TITLE = 'Equivalent Resistance of Resistors in Parallel | Student Calculator';
function Add() {
  const [name1, setName1] = useState("");

  const [headingText, setHeading] = useState("");
  const [number, setNumber] = useState("");


  function handleChange1(event) {
    console.log(event.target.value);
    setName1(event.target.value);
  }
  function handlereset() {
    setName1("");
    setHeading("");
    setNumber("");
    
  }

  function handleClick(event) {
    var result = name1.split(' ');
    var sum=0;
    for(var i=0; i<result.length; i++){
      sum += parseFloat(result[i]);
    }
    setNumber(i);
    setHeading(sum+" Ω");

    event.preventDefault();
  }

  return (
    <div>
            <Insidenav />
            <Helmet>
          <title>{ TITLE }</title>
        </Helmet>
    <div className="container-fluid calci">

      <h1 className="calci">Sum of {number} Resistances in series = {headingText}</h1>
      <form onSubmit={handleClick} autoComplete="off" >
      <label for="bin">Enter value of each resistance separated by a space in ohms</label>
        <input
          className="calciipinfix"
          onChange={handleChange1}
          type="text"
          placeholder=""
          value={name1}
          id="bin"
          required
        />
      
      
        <button className="calcibutton" type="submit">Find</button>
        <button onClick={handlereset} className="calcibutton" type="reset">Reset</button>

      </form>
      <div className="">   
        <p className="suggestion">SUGGESTED CALCULATORS FOR YOU</p>
        <Link to="/maths/two-unknown-calculator"><button className="btn btn-light mr-3">Two Unknown Calculator</button></Link>
        <Link to="/maths/three-unknown-calculator"><button className="btn btn-light mr-3">Three Unknown Calculator</button></Link>
        <Link to="/electronics/star-to-delta"><button className="btn btn-light mr-3">Star to Delta</button></Link>
        <Link to="/electronics/parallel-resistance-calculator"><button className="btn btn-light mr-3">Parallel Resistance Calculator</button></Link>
        <Link to="/electronics/XL-and-XC"><button className="btn btn-light mr-3">Inductance and Capacitance</button></Link>
        <Link to="/electronics/magnitude-and-phase"><button className="btn btn-light mr-3">Magnitude and Phase</button></Link>


      </div>
    </div>
    <div className="container mt-3 mb-3">
      <h4>How to use this ?</h4>
      <p3>If you have 4 resistances with values R1 = 1.245, R2 = 14.21, R3 = 8.14, R4 = 9.651 and you want to find the equivalent resistance when they are arranged in series, then you need to type the value of each resistance in the box separated by a space. <br /><br /> For the example given above, you must type 1.245 14.21 8.14 9.651 which will give you the answer.</p3>
     

    </div>
    </div>
  );
}

export default Add;
