import React, { useState } from "react";
import Insidenav from "../insidenav";
import { Link } from "react-router-dom";
import { Helmet } from 'react-helmet'
const TITLE = 'XL and XC Calculator | Student Calculator';
function Add() {
  const [name1, setName1] = useState("");
  const [name2, setName2] = useState("");
  const [name3, setName3] = useState("");


  const [headingText1, setHeading1] = useState("");
  const [headingText2, setHeading2] = useState("");

  function handlereset() {
    setName1("");
    setName2("");
    setName3("");
    setHeading1("");
    setHeading2("");


  }
  function handleChange1(event) {
    console.log(event.target.value);
    setName1(event.target.value);
  }
  function handleChange2(event) {
    console.log(event.target.value);
    setName2(event.target.value);
  }
  function handleChange3(event) {
    console.log(event.target.value);
    setName3(event.target.value);
  }

  function handleClick(event) {
    if(name2=="" && name3==""){
      handlereset();
    }
    else{
    setHeading1((name1*name3).toFixed(4) +" j Ω");
    setHeading2(-1*(1/name1*name2).toFixed(4) +" j Ω");
    }
    event.preventDefault();
  }

  return (
    <div>
             <Insidenav />
             <Helmet>
          <title>{ TITLE }</title>
        </Helmet>
    <div className="container-fluid calci">
      <h1 className="calci">X<sub>L</sub> = {headingText1}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;X<sub>c</sub> = {headingText2}&nbsp;</h1>
      <form onSubmit={handleClick}>
      <div className="inner">
        <label className="para1" for="x1"> ω&nbsp;&nbsp;&nbsp; </label>
        <input
          className="calciipunknown para2"
          onChange={handleChange1}
          type="number"
          placeholder=""
          value={name1}
          name="x1"
          step="any"
          required
        />
        </div>
        <div className="inner">
        <label className="para1" for="x1">C&nbsp;&nbsp;&nbsp; </label>
        <input
          className="calciipunknown para2"
          onChange={handleChange2}
          type="number"
          placeholder=""
          step="any"
          value={name2}
          
        />
        </div>
        <div className="inner">
        <label className="para1" for="x1">L&nbsp;&nbsp;&nbsp;</label>
        <input
          className="calciipunknown para2"
          onChange={handleChange3}
          type="number"
          placeholder=""
          step="any"
          value={name3}
          
        />
        </div>
        <button className="calcibutton" type="submit">Find</button>
        <button onClick={handlereset} className="calcibutton" type="reset">Reset</button>

      </form>
      <div className="">   
        <p className="suggestion">SUGGESTED CALCULATORS FOR YOU</p>
        <Link to="/maths/two-unknown-calculator"><button className="btn btn-light mr-3">Two Unknown Calculator</button></Link>
        <Link to="/maths/three-unknown-calculator"><button className="btn btn-light mr-3">Three Unknown Calculator</button></Link>
        <Link to="/electronics/star-to-delta"><button className="btn btn-light mr-3">Star to Delta</button></Link>
        <Link to="/electronics/parallel-resistance-calculator"><button className="btn btn-light mr-3">Parallel Resistance Calculator</button></Link>
        <Link to="/electronics/magnitude-and-phase"><button className="btn btn-light mr-3">Magnitude and Phase</button></Link>


      </div>
      </div>
      <div className="container mt-3 mb-3">
      <h4>How to Use this ?</h4>
      <p3>Enter in any one field and then click find. If you type in more than one box, then you will not find any answer. Most importantly, make sure you have typed in only one box. Otherwise click reset and then continue.</p3>
     
    </div>
    </div>
  );
}

export default Add;
