import React, { useState } from "react";
import Insidenav from "../insidenav";
import { Link } from "react-router-dom";
import { Helmet } from 'react-helmet'
const TITLE = 'Angle Converter | Student Calculator';
var convert = require('convert-units')


function Add() {
  
  const [name1, setName1] = useState("");
  const [name2, setName2] = useState("");
  const [name3, setName3] = useState("");
  const [name4, setName4] = useState("");
  const [name5, setName5] = useState("");
 

 
  const { Convert } = require ('any-to-any');

  function handleChange1(event) {
    console.log(event.target.value);
    setName1(event.target.value);
    
  }function handleChange2(event) {
    console.log(event.target.value);
    setName2(event.target.value);
    
  }
  function handleChange3(event) {
    console.log(event.target.value);
    setName3(event.target.value);
    
  }
  function handleChange4(event) {
    console.log(event.target.value);
    setName4(event.target.value);
  }
  function handleChange5(event) {
    console.log(event.target.value);
    setName5(event.target.value);
  
  }
 
  function handlereset() {
    setName1("");
    setName2("");
    setName3("");
    setName4("");
    setName5("");
   
    
  }
  
 
  function handleClick(event) {
    if(name1!="" ||name2!="" ||name3!="" ||name4!="" ||name5!=""){
      var entered = 0;
    if(name1 != ""){entered++;}
    if(name2 != ""){entered++;}
    if(name3 != ""){entered++;}
    if(name4 != ""){entered++;}
    if(name5 != ""){entered++;}
    
    if(entered != 1){
      handlereset();
    }
    else{
    if(name3 != ""){var Input = name3; var From = 'grad';}
    if(name4 != ""){var Input = name4; var From = 'arcmin';}
    if(name5 != ""){var Input = name5; var From = 'arcsec';}
    
    if(name1 != ""){var Input = name1; var From = 'deg';}
    if(name2 != ""){var Input = name2; var From = 'rad';}
    setName1(convert(Input).from(From).to('deg'));
    setName2(convert(Input).from(From).to('rad'));
    setName3(convert(Input).from(From).to('grad'));
    setName4(convert(Input).from(From).to('arcmin'));
    setName5(convert(Input).from(From).to('arcsec'));
    
    }}
     event.preventDefault();
    
  }

  return (
    <div>
      <Insidenav />
      <Helmet>
          <title>{ TITLE }</title>
        </Helmet>

    <div className="container-fluid calci">

      <h1 className="calci">Enter in Any one of the following and click find</h1>

      <form onSubmit={handleClick} autoComplete="off">
      <label for="bin">degrees ( ° )⇃↾</label>
      <input className="calciipsmall"  type="number" step="any" id="bin" onChange={handleChange1} value={name1} />
      <label for="oct">radians ( rad ) ⇃↾</label>
      <input className="calciipsmall"  type="number" step="any" id="oct" onChange={handleChange2} value={name2} />
      <label for="dec">gradians ( grad ) ⇃↾</label>
      <input className="calciipsmall" type="number" step="any" id="dec" onChange={handleChange3} value={name3} />
      <label for="dec">minute of arc ( arcmin ) ⇃↾</label>
      <input className="calciipsmall"  type="number" step="any" id="dec" onChange={handleChange4} value={name4} />
      <label for="dec">second of arc ( arcsec ) ⇃↾</label>
      <input className="calciipsmall"  type="number" step="any" id="dec" onChange={handleChange5} value={name5} />
      
        
        <button className="calcibutton" type="submit">Find Rest All</button>
        <button onClick={handlereset} className="calcibutton" type="reset">Reset</button>

      </form>
      <div className="">   
        <p className="suggestion">SUGGESTED CALCULATORS FOR YOU</p>
        <Link to="/advanced-converters"><button className="btn btn-light mr-3">More Converters Like this</button></Link>
        <Link to="/basic-converters"><button className="btn btn-light mr-3">Basic Converters</button></Link>
       

      </div>
    </div>
    <div className="container mt-3 mb-3">
    <h4>How to Use this ?</h4>
      <p3>Enter in any one field and then click find. If you type in more than one box, then you will not find any answer. Most importantly, make sure you have typed in only one box. Otherwise click reset and then continue.</p3>
     
    </div>
    </div>
  );
}

export default Add;
