import React, { useState } from "react";
import Insidenav from "../insidenav";
import { Link } from "react-router-dom";
import { Helmet } from 'react-helmet'
const TITLE = 'Infix to Postfix Converter | Student Calculator';
const infixToPostfix = require('infix-to-postfix');

function Add() {
  const [name1, setName1] = useState("");
  

  const [headingText, setHeading] = useState("");

  function handleChange1(event) {
    console.log(event.target.value);
    setName1(event.target.value);
  }
  function handlereset() {
    setName1("");
    setHeading("");
  }

  function handleClick(event) {
    
    setHeading(infixToPostfix(name1));
    event.preventDefault();
  }

  return (
    <div>
             <Insidenav />
       <Helmet>
          <title>{ TITLE }</title>
        </Helmet>
    <div className="container-fluid calci">
      <h1 className="calci">Your Postfix Expression = {headingText}</h1>
      <form onSubmit={handleClick}>
        
        <input
          className="calciipinfix"
          onChange={handleChange1}
          type="text"
          placeholder="Enter Your INFIX expression here"
          value={name1}
          required
        />
        
        <button className="calcibutton" type="submit">Find</button>
        <button onClick={handlereset} className="calcibutton" type="reset">Reset</button>

      </form>
      <div className="">   
        <p className="suggestion">SUGGESTED CALCULATORS FOR YOU</p>
        <Link to="/cse/infix-to-prefix"><button className="btn btn-light mr-3">Infix to prefix</button></Link>
        <Link to="/cse/binary-to-all"><button className="btn btn-light mr-3">Binary to Decimal to Hex to Oct</button></Link>
        <Link to="/cse/any-base-finder"><button className="btn btn-light mr-3">Any Base Converter</button></Link>
       

      </div>
    </div>
    <div className="container mt-3 mb-3">
    <h4>About Postfix Expressions</h4>
    <p3>An expression is called a postfix expression if the operator in the expresssion appears after the operands.</p3>
    <br /><br /><h4>Example</h4>
    <p3>12+ is the postfix expression for the infix expression 1+2</p3>
    <br /><br /><h4>Why should we convert ?</h4>
    <p3>The Infix expression is not good when you tell a machine to process the output. This is because the postfix and prefix expression donot require any kind of operator precedence rule and it is easy for the machine to process.</p3>

  </div>
  </div>
  );
}

export default Add;
