import React, { useState } from "react";
import Insidenav from "../insidenav";
import { Link } from "react-router-dom";

import { Helmet } from 'react-helmet'
const TITLE = 'Compound Interest Calculator | Student Calculator';
function Add() {
  
  const [name3, setName3] = useState("");
  const [name4, setName4] = useState("");
  const [name5, setName5] = useState("");
  const [name6, setName6] = useState("");

  const [headingText, setHeading] = useState("");
  const [headingText2, setHeading2] = useState("");



 

  
  function handleChange3(event) {
    console.log(event.target.value);
    setName3(event.target.value);
    
  }
  function handleChange4(event) {
    console.log(event.target.value);
    setName4(event.target.value);
  }
  function handleChange5(event) {
    console.log(event.target.value);
    setName5(event.target.value);
  
  }
  function handleChange6(event) {
    console.log(event.target.value);
    setName6(event.target.value);
  
  }
  function handlereset() {
    setName3("");
    setName4("");
    setName5("");
    setName6("");

    setHeading("");
    setHeading2("");
    
  }
  
 
  function handleClick(event) {
    var r = name4 / 100;
    var raise = name5 * name6;
    var inside = 1+(r/name5);
    var tot = Math.pow(inside,raise);
    var A = Math.round(name3 * tot);
    var B = A - name3;
   setHeading("₹ "+A);
     setHeading2("₹ "+B);
  

   

   
     event.preventDefault();
    
  }

  return (
    <div>
      <Insidenav />
      <Helmet>
          <title>{ TITLE }</title>
        </Helmet>
    <div className="container-fluid calci">

      <h1>Amount = {headingText} <br />Net Return = {headingText2}</h1>
 


      <form onSubmit={handleClick} autoComplete="off" required>
      <label for="bin">Principal Amount ( ₹ )</label>
      <input className="calciipsmall" placeholder="" type="number" id="bin" onChange={handleChange3} value={name3} required />
      <label for="oct">Annual Rate ( % )</label>
      <input className="calciipsmall" placeholder="" type="number" id="oct" onChange={handleChange4} value={name4} required />
      <label for="dec">Compounds Per Year</label>
      <input className="calciipsmall" placeholder="" type="number" id="dec" onChange={handleChange5} value={name5} required />
      <label for="dec">Years</label>
      <input className="calciipsmall" placeholder="" type="number" id="dec" onChange={handleChange6} value={name6} required />
      
        
        <button className="calcibutton" type="submit">Find Rest All</button>
        <button onClick={handlereset} className="calcibutton" type="reset">Reset</button>

      </form>
      <div className="">   
        <p className="suggestion">SUGGESTED CALCULATORS FOR YOU</p>
        <Link to="/basic-converters/time-converters"><button className="btn btn-light mr-3">Time and Date Converter</button></Link>
        <Link to="/finance/return-of-investment"><button className="btn btn-light mr-3">Return of Investment</button></Link>
        <Link to="/finance/simple-interest"><button className="btn btn-light mr-3">Simple Interest Calculator</button></Link>



      </div>
    </div>
    <div className="container mt-3 mb-3">
    <h4>About Compound Interest</h4>
      <p3>Compound interest is calculated using Principal amount, annual interest rate, compounds per year and years. <br /><br />IMPORTANT NOTE : The values are rounded off and are not perfectly accurate. If the net return is rs. 79999.6564, then it is printed as rs. 80000.</p3>
      
    </div>
    </div>
  );
}

export default Add;
