import React, { useState } from "react";
import Insidenav from "../insidenav";
import { Link } from "react-router-dom";
import { Helmet } from 'react-helmet'
const TITLE = 'Any Base Converter | Student Calculator';
function Add() {
  
  const [name3, setName3] = useState("");
  const [name4, setName4] = useState("");
  const [name5, setName5] = useState("");
  const [name6, setName6] = useState("");


  const [headingText, setHeading] = useState("");

  const { Convert } = require ('any-to-any');

  
  function handleChange3(event) {
    console.log(event.target.value);
    setName3(event.target.value);
    
  }
  function handleChange4(event) {
    console.log(event.target.value);
    setName4(event.target.value);
  }
  function handleChange5(event) {
    console.log(event.target.value);
    setName5(event.target.value);
  
  }
  function handlereset() {
    setName3("");
    setName4("");
    setName5("");
    setHeading("");
  }
  
  function convert(value, base = 2) {
    var [integer, fraction = ''] = value.toString().split('.');

    return parseInt(integer, base) + (integer[0] !== '-' || -1) * fraction
        .split('')
        .reduceRight((r, a) => (r + parseInt(a, base)) / base, 0);
}
  function handleClick(event) {
    var decimalFormat = convert(name3, name4);
    setName6(decimalFormat.toString(name5));
    setHeading("Number "+name3+" from base "+name4+" to "+name5+" is given below");

   
   
     event.preventDefault();
    
  }

  return (
    <div>
      <Insidenav />
      <Helmet>
          <title>{ TITLE }</title>
        </Helmet>
    <div className="container-fluid calci">

      <h1 className="calci">This is Any base Finder. Enter to Find</h1>

      <form onSubmit={handleClick} autoComplete="off">
      <label for="bin">Enter Number ⇃↾</label>
      <input className="calciipsmall" placeholder="Enter Number" type="text" id="bin" onChange={handleChange3} value={name3} required/>
      <label for="oct">From Base [ 1 - 36 ] ⇃↾</label>
      <input className="calciipsmall" min="1" max="36" placeholder="Enter From Base" type="number" step="any" id="oct" onChange={handleChange4} value={name4} required/>
      <label for="dec">To Base [ 1 - 36 ] ⇃↾</label>
      <input className="calciipsmall" min="1" max="36" placeholder="Enter to Base" type="number" step="any" id="dec" onChange={handleChange5} value={name5} required/>
  <label for="dc">{headingText}</label>
      <input className="calciipsmall" placeholder="Answer" type="text" id="dc" value={name6} />
     
        
        <button className="calcibutton" type="submit">Find Rest All</button>
        <button onClick={handlereset} className="calcibutton" type="reset">Reset</button>

      </form>
      <div className="">   
        <p className="suggestion">SUGGESTED CALCULATORS FOR YOU</p>
        <Link to="/cse/binary-to-all"><button className="btn btn-light mr-3">Binary to Decimal to Hex to Oct</button></Link>
        <Link to="/cse/infix-to-postfix"><button className="btn btn-light mr-3">Infix to postfix</button></Link>
        <Link to="/cse/infix-to-prefix"><button className="btn btn-light mr-3">Infix to prefix</button></Link>
        <Link to="/basic-converters/memory-size-converters"><button className="btn btn-light mr-3">Memory Size converters</button></Link>

      </div>
    </div>
    <div className="container mt-3 mb-3">
      <h4>How to use the calcualtor</h4>
      <p3>In the first box enter the number in any format like decimal binary octal hexadecimal or in any base. Make sure the number is valid. After this Enter the base of the number you entered. It should be within one and 36. Also enter the base to which the input number is to be converted. After clicking find, You will get the answer in fourth box. Make sure the number, base numbers all are entered properly.</p3>
      
    </div>
    </div>
  );
}

export default Add;
