import React, { useState } from "react";
import Insidenav from "../insidenav";
import { Link } from "react-router-dom";
import { Helmet } from 'react-helmet'
const TITLE = 'Binary to Decimal to Hexadecimal to Octal | Student Calculator';
function Add() {
  
  const [name3, setName3] = useState("");
  const [name4, setName4] = useState("");
  const [name6, setName6] = useState("");
  const [name5, setName5] = useState("");

 
  const { Convert } = require ('any-to-any');

  
  function handleChange3(event) {
    console.log(event.target.value);
    setName3(event.target.value);
    
  }
  function handleChange4(event) {
    console.log(event.target.value);
    setName4(event.target.value);
  }
  function handleChange5(event) {
    console.log(event.target.value);
    setName5(event.target.value);
  
  }
  function handlereset() {
    setName3("");
    setName4("");
    setName5("");
    setName6("");
    
  }
  function handleChange6(event) {
    console.log(event.target.value);
    setName6(event.target.value);
  }
  function convert(value, base = 2) {
    var [integer, fraction = ''] = value.toString().split('.');

    return parseInt(integer, base) + (integer[0] !== '-' || -1) * fraction
        .split('')
        .reduceRight((r, a) => (r + parseInt(a, base)) / base, 0);
}
  function handleClick(event) {

    if(name3=="" && name4=="" && name5 =="" && name6 ==""){

    }
    if(name3!="" || name4!="" || name5 != "" || name6 !=""){
      var entered=0;
      if(name3!=""){entered++;}
      if(name4!=""){entered++;}
      if(name5!=""){entered++;}
      if(name6!=""){entered++;}
      if(entered != 1){
        handlereset();
      }
      else{
        if(name5!="")
    {
      var InputNumber  = parseFloat(name5);
      var bin = InputNumber.toString(2);
      setName3(bin);
    var dec = InputNumber.toString(10);
      setName5(dec);
    var oct= InputNumber.toString(8);
      setName4(oct);
    var hex = InputNumber.toString(16).toUpperCase();
      setName6(hex);
    }
    else if(name3!="")
    {
      var answer = convert(name3);
      setName5(answer);
      setName4(answer.toString(8));
      setName6(answer.toString(16).toUpperCase());
      setName3(name3);
      
     
    }
    else if(name4!="")
    {
      var answer = convert(name4, 8);
      setName5(answer);
      setName4(name4);
      setName6(answer.toString(16).toUpperCase());
      setName3(answer.toString(2));
     
      
    }
    else if(name6!="")
    {
      var answer = convert(name6, 16);
      setName5(answer);
      setName3(answer.toString(2));
      setName4(answer.toString(8));
      setName6(name6);
    }


      }


    }
    
   
     event.preventDefault();
    
  }

  return (
    <div>
      <Insidenav />
      <Helmet>
          <title>{ TITLE }</title>
        </Helmet>
    <div className="container-fluid calci">

      <h1 className="calci">Enter in Any one of the following and click find</h1>

      <form onSubmit={handleClick} autoComplete="off">
      <label for="bin">Binary ⇃↾</label>
      <input className="calciipsmall" placeholder="Enter Binary" type="number" step="any" id="bin" onChange={handleChange3} value={name3} />
      <label for="oct">Octal ⇃↾</label>
      <input className="calciipsmall" placeholder="Enter Octal" type="number" step="any" id="oct" onChange={handleChange4} value={name4} />
      <label for="dec">Decimal ⇃↾</label>
      <input className="calciipsmall" placeholder="Enter Decimal" type="number" step="any" id="dec" onChange={handleChange5} value={name5} />
      <label for="hex">HexaDecimal ⇃↾</label>
      <input className="calciipsmall" placeholder="Enter HexaDecimal" type="text" step="any" id="hex" onChange={handleChange6} value={name6} />
        
        <button className="calcibutton" type="submit">Find Rest All</button>
        <button onClick={handlereset} className="calcibutton" type="reset">Reset</button>

      </form>
      <div className="">   
        <p className="suggestion">SUGGESTED CALCULATORS FOR YOU</p>
        <Link to="/cse/hanoi-steps"><button className="btn btn-light mr-3 ">Any Base Converter</button></Link>
        <Link to="/cse/infix-to-postfix"><button className="btn btn-light mr-3">Infix to Postfix</button></Link>
        <Link to="/cse/infix-to-prefix"><button className="btn btn-light mr-3">Infix to Prefix</button></Link>
        <Link to="/basic-converters/memory-size-converters"><button className="btn btn-light mr-3">Memory Size Converter</button></Link>

      </div>
    </div>
    <div className="container mt-3 mb-3">
      <h4>About the number systems</h4>
      <p3>The binary is essential for machines. Octal uses only 3 bits and HexaDecimal is used to represent large binary numbers in short. Decimal is what humans use to represent numbers. </p3>
      <br /><br />
      <h4>How to Use this Calculator</h4>
      <p3>Enter in any of the boxes given above. After pressing find you will get the answers for rest all formats. If you want to find the answer for one more number please click reset and then proceed. Typing in a different field without pressing reset won't give proper answers. </p3>
      
    </div>
    </div>
  );
}

export default Add;
